@import "../inc/config";

.screen_tables {
  flex-direction: column;

  li.table_line {
    display: flex;
    align-items: center;
    padding: 10px 5px;
    margin: 5px;
    background: $color1;
    min-height: 65px;
    border-radius: 15px;

    .num {
      width: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      font: 400 14px/16px $font;
      color: $color2;
    }

    .date {
      // text-transform: uppercase;
      font: 400 14px/16px $font;
      display: flex;
      flex: 1;
    }

    .status {
      display: flex;
      width: 40px;

      i {
        color: $color2;
      }
    }
  }
}

.screen_tables_in {
  flex-direction: column;

  .bar {
    padding: 20px;

    h1 {
      color: $color2;
      font: 500 21px/16px $font;

      span {
        color: #fff;
      }
    }
  }
  .times {
    display: flex;
    flex-wrap: wrap;
    margin:20px 0;
    .uk-margin {
      margin: 0 !important;
      width: 33.333%;
              padding-right: 10px;
      &:nth-child(3) {
        padding: 0;
      }
    }
  }
  form {
    padding: 20px;
  }
  button {
    margin-top: 10px;
  }
  label {
    color: #fff;
    font: 500 12px/16px $font;
    margin: 0 5px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  input,
  select {
    margin-top: 10px;
    font: 400 15px/16px $font;
  }
}
